
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
import { stream, cates } from "./FC_Data";

export default defineComponent({
  name: "stream",
  setup() {
    const store = useStore();
    const screenWidth = computed(() => store.state.device.screenWidth);
    const isMobile = computed(() => store.state.device.isMobile);
    const active = ref(0);
    const hover = ref(0);

    return {
      screenWidth,
      isMobile,
      cates,
      stream,
      active,
      hover,
    };
  },
});
