    cate: 0,
    key: "fjc",
    title: "防劫持教程",
    html:
      '<div class="CY_C7"><div><div class="CY_EB"><h3>电脑端防劫持教程</h3><p>连接到WI-FI网络，才能更改设备的DNS设置。</p><p>电脑端推荐：首选DNS服务器 114.114.114.114，备用DNS服务器8.8.8.8</p><img src="https://webstatic.0am08m.com/assets/help/antiHijack.png.webp?version=1616864075706" class="CY_E7"></div></div></div>',
