import {
  computed,
  onMounted,
  defineComponent,
  reactive,
  toRefs,
  ref,
} from "vue";
import { useStore } from "vuex";
import inputForm from "./form";
import { useRoute, useRouter } from "vue-router";
import { typeCreate } from "../../service/casinoPaymentFlow/stored/create";

/**
 * 登入功能
 */
export default () => {
  /** 登入表單 */
  const form = ref({
    [inputForm.method.id]: "",
    [inputForm.amount.id]: "",
  });

  const $route = useRoute();
  const $store = useStore();

  const list = computed(() => $store.getters["deposit/list"]);

  const selected = computed(() => {
    const list = $store.getters["deposit/list"];
    const METHOD = form.value[inputForm.method.id];

    const result = list.find((l: { method: string }) => {
      return l.method === METHOD;
    });
    if (result) return result;
    return {};
  });

  /** 取得儲值單資訊 */
  const getInfo = async () => {
    await $store.dispatch("deposit/getInfo");
  };

  /** 送出表單 */
  const submitForm = async () => {
    await $store.dispatch("deposit/create", form.value);
  };

  onMounted(() => {});

  return {
    inputForm,
    form,
    getInfo,
    submitForm,
    list,
    selected,
  };
};
