<template>
  <el-form @submit.prevent="handleSubmitForm" :model="form" ref="FORM">
    <div class="bg-white rounded m-3 p-3 border">
      <h4>{{ $i18n.global.t("CasinoCore.stored") }}</h4>
      <p class="mb-3">{{ $i18n.global.t("CasinoCore.storedMethod") }}</p>
      <ul class="list-unstyled row">
        <li v-for="(method, i) in list" :key="`method_${i}`" class="col-3">
          <label
            :class="
              form[inputForm.method.id] === method.method
                ? 'border-primary'
                : 'border-grey'
            "
            class="method bg-white d-block text-center border rounded p-3"
            role="button"
          >
            <input
              @change="handleChange"
              v-model="form[inputForm.method.id]"
              :value="method.method"
              required
              type="radio"
              class="no-show"
            />
            {{ $i18n.global.t(method.label) }}
          </label>
        </li>
      </ul>
    </div>
    <div class="bg-white rounded m-3 p-3 border">
      <input v-model="form[inputForm.method.id]" required type="hidden" />
      <el-form-item
        :label="$i18n.global.t(inputForm.amount.label)"
        :prop="inputForm.amount.id"
      >
        <el-input
          v-model="form[inputForm.amount.id]"
          :placeholder="$i18n.global.t('CasinoCore.storedAmount')"
          required
          type="number"
          :min="selected.min"
          :max="selected.max"
        />
      </el-form-item>
      <div class="text-center">
        <el-button type="success" native-type="submit">
          {{ $i18n.global.t("CasinoCore.stored_now") }}
        </el-button>
      </div>
    </div>
  </el-form>
</template>

<script lang='ts'>
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
  watch,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
import { ElForm } from "node_modules/element-ui/types/form";
import { ElMessage } from "element-plus";
import { useI18n } from "vue-i18n";
import desposit from "@/modules/FPKG-240000-CasinoFrontApi/composition/deposit/index";

export default defineComponent({
  name: "desposit",
  setup() {
    const store = useStore();
    const screenWidth = computed(() => store.state.device.screenWidth);
    const isMobile = computed(() => store.state.device.isMobile);
    const i18n = useI18n();
    const { getInfo, list, form, inputForm, submitForm, selected } = desposit();

    const handleChange = () => {
      form.value[inputForm.amount.id] = selected.value.min;
    };

    const initSelected = (v: { method: string; min: string }[]) => {
      form.value[inputForm.method.id] = v[0].method;
      form.value[inputForm.amount.id] = v[0].min;
    };
    watch(list, initSelected);

    onMounted(async () => {
      await getInfo();
    });

    return {
      screenWidth,
      isMobile,
      list,
      form,
      inputForm,
      submitForm,
      i18n,
      selected,
      handleChange,
    };
  },
  methods: {
    async handleSubmitForm() {
      const myForm = this.$refs.FORM as ElForm;
      const valid = await myForm.validate();

      if (valid) {
        await this.submitForm();
        this.$router.push("/app/myCenter/depositResult");
      } else {
        ElMessage.error(this.i18n.t("Util.validateError"));
      }
    },
  },
});
</script>

<style lang="scss" scoped>
.method {
  transition: all 0.3s;
  &:hover {
    border-color: var(--bs-primary) !important;
    background-color: darken(#fff, 3%) !important;
  }
}
</style>