
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
/**
 * 頂部工具列上的使用者資訊
 */
export default defineComponent({
  name: "navbar userInfo",
  setup() {
    const $store = useStore();
    const screenWidth = computed(() => $store.state.device.screenWidth);
    const isMobile = computed(() => $store.state.device.isMobile);

    const logout = () => $store.dispatch("account/logout");

    return {
      screenWidth,
      isMobile,
      logout,
    };
  },
});
