
import { defineComponent, computed, onMounted, toRefs } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import LOGIN from "@/modules/FPKG-240000-CasinoFrontApi/composition/account/login";

export default defineComponent({
  name: "login",
  setup() {
    const { form, inputForm, init, submitForm } = LOGIN();
    const $router = useRouter();
    const $store = useStore();
    const screenWidth = computed(() => $store.state.device.screenWidth);
    const isMobile = computed(() => $store.state.device.isMobile);

    const handleSubmit = async () => {
      await submitForm();
      $router.push("/");
    };

    onMounted(() => {
      init();
    });

    return {
      screenWidth,
      isMobile,
      form,
      inputForm,
      handleSubmit,
    };
  },
});
