import {
  computed,
  onMounted,
  defineComponent,
  reactive,
  toRefs,
  ref,
} from "vue";
import { useStore } from "vuex";
import inputForm from "./form";
import { useRoute, useRouter } from "vue-router";

/**
 * 登入功能
 */
export default () => {
  /** 登入表單 */
  const form = ref({
    [inputForm.account.id]: "",
    [inputForm.password.id]: "",
    [inputForm.loginVerify.id]: "",
  });

  /** 記住帳號 */
  const isSaveAccount = ref(false);

  const $route = useRoute();

  const $store = useStore();
  const branch = computed(() => $store.getters.branch.branch);
  const canRegister = computed(() => $store.getters.branch.register);
  const validType = computed(() => $store.getters.branch.validType);
  const captcha = computed(() => $store.getters.account.captcha);

  /** 是否需要驗證 */
  const isValidNeeded = computed(() => {
    return $store.getters.branch.branch.p17_ebs_login_valid_type !== null;
  });

  /** 驗證的提示 */
  const validLabel = computed(() => {
    const type = $store.getters.branch.branch.p17_ebs_login_valid_type;
    if (type) {
      return `CasinoCore.${type}`;
    }
    return "";
  });

  /** 客服url */
  const custServiceUrl = computed(() => {
    return $store.getters.branch.branch.branch.p17_ebs_customer_service_path;
  });

  /** 記住帳號並存在local */
  const setSaveAccount = () => {
    const canSave = isSaveAccount.value === true;
    if (canSave) {
      const account = form.value[inputForm.account.id];
      window.localStorage.setItem("account", account);
    }
  };

  /** 取回記住的帳號 */
  const getSaveAccount = () => {
    const query = $route.query.account as string;
    const local = window.localStorage.getItem("account");
    const account = query || local;
    if (account) {
      form.value[inputForm.account.id] = account;
    }
  };

  /** 取得驗證碼圖片 */
  const getCaptcha = async () => {
    form.value[inputForm.loginVerify.id] = "";
    await $store.dispatch("account/captcha");
  };

  /** 送出表單 */
  const submitForm = async () => {
    try {
      await $store.dispatch("account/login", form.value);
      setSaveAccount();
      form.value = {};
    } catch (e) {
      localStorage.setItem(
        "verification-acount",
        form.value[inputForm.account.id],
      );

      // if (e.data.code === 140055) await getCaptcha();
      throw e;
    }
  };

  const init = () => {
    // TODO: 這邊可能捨棄 hud
    // $store.dispatch("hud/delAccount");
    getSaveAccount();
    getCaptcha();
  };

  onMounted(() => {
    // TODO: 這邊可能捨棄 hud
    // $store.dispatch("hud/delAccount");
    getSaveAccount();
    getCaptcha();
  });

  return {
    inputForm,
    form,
    init,
    submitForm,
  };
};
