
import { defineComponent, computed } from "vue";
import { useStore } from "vuex";
import webLayout from "../components/layout/layout.vue";
import mobileLayout from "../components/layout/layout.vue";

export default defineComponent({
  name: "MT",
  components: {
    webLayout,
    mobileLayout,
  },
  setup() {
    const $store = useStore();
    const screenWidth = computed(() => $store.state.device.screenWidth);
    const isMobile = computed(() => $store.state.device.isMobile);

    return {
      screenWidth,
      isMobile,
    };
  },
});
