
import { defineComponent, computed, ref } from "vue";
import { useStore } from "vuex";
import userInfo from "./userInfo/index.vue";
import SPORT from "./subMenu/sport.vue";

const MENU = [
  { name: "體育", href: "/app/sports" },
  { name: "真人", href: "/app/realbet" },
  { name: "棋牌", href: "/app/chessGame" },
  { name: "電競", href: "/app/gaming" },
  { name: "彩票", href: "/app/lottery" },
  { name: "電子", href: "/app/egame/concise" },
  { name: "贊助", href: "/" },
  { name: "優惠", href: "/" },
  { name: "合營", href: "/" },
  { name: "APP下載", href: "/" },
];

/** 子選單排序 */
const SUB_MENU = [
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
  "SPORT",
];

export default defineComponent({
  name: "navbar",
  components: { userInfo, SPORT },
  setup() {
    const active = ref(null);
    const $store = useStore();
    const screenWidth = computed(() => $store.state.device.screenWidth);
    const isMobile = computed(() => $store.state.device.isMobile);
    const isLogined = computed(() => $store.getters["account/isLogined"]);

    const handleClose = () => {
      active.value = null;
    };
    return {
      screenWidth,
      isMobile,
      MENU,
      active,
      handleClose,
      SUB_MENU,
      isLogined,
    };
  },
});
