
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
import { hotGames, hotGameCates } from "./FC_Data";

export default defineComponent({
  name: "Hot games",
  setup() {
    const store = useStore();
    const screenWidth = computed(() => store.state.device.screenWidth);
    const isMobile = computed(() => store.state.device.isMobile);
    const active = ref(0);
    const hover = ref(0);

    const setBg = ($e: any, style: string) => {
      const I = $e.currentTarget.querySelector("i");
      I.style = style;
    };
    return {
      screenWidth,
      isMobile,
      active,
      hover,
      setBg,
      hotGames,
      hotGameCates,
    };
  },
});
