
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
import { cates, articles } from "./FC_Data";

const MENU = cates.map((c, i) => {
  const R: any = c;
  R.articles = articles.filter((a) => a.cate === i);
  return R;
});

export default defineComponent({
  name: "help",
  setup() {
    const store = useStore();
    const screenWidth = computed(() => store.state.device.screenWidth);
    const isMobile = computed(() => store.state.device.isMobile);

    const ACTIVE_ARTICLE = ref({});
    ACTIVE_ARTICLE.value = articles[0];
    // const articles =

    return {
      screenWidth,
      isMobile,
      MENU,
      ACTIVE_ARTICLE,
    };
  },
});
