<template>
  <div class="RG_Rc">
    <div class="Jt_Jv">
      <div class="Jt_Jz">
        <img
          src="https://webstatic.0am08m.com/assets/v4_home/quality-services@2x.png"
          class="Jt_J1"
        />
        <h2 class="Jt_J3"><span></span><span>优质服务</span><span></span></h2>
        <p class="Jt_J5">亚博全心全意为您提供最优质的服务</p>
      </div>
      <div class="Kj_Kl Jt_Jx"><ul class="Kj_Kn"></ul></div>
      <div class="Jt_J7">
        <div class="Jt_KK" style="height: 580px">
          <div class="Jt_KO">
            <div class="RG_TK">
              <div class="RG_TM">
                <ul ref="clock" class="RG_TO">
                  <li
                    v-for="(adv, i) in advages"
                    :key="`adv_${i}`"
                    class="RG_TQ"
                  >
                    <div class="RG_TS">
                      <div class="RG_TU">
                        <span :data-count="adv.count" data-cur="0">{{
                          adv.count
                        }}</span
                        >{{ adv.unit }}
                      </div>
                      <div class="RG_TW">
                        <div class="RG_TY"><div></div></div>
                        <div class="RG_Ta"><div></div></div>
                      </div>
                    </div>
                    <p class="RG_Al">{{ adv.title }}</p>
                    <p class="RG_TE">{{ adv.titleEn }}</p>
                  </li>
                </ul>
              </div>
              <div class="RG_Tp">
                <ul class="RG_Tr">
                  <li
                    v-for="(adv, i) in subAdvages"
                    :key="`subAdv_${i}`"
                    class="RG_Tt"
                  >
                    <img :src="adv.src" />
                    <div class="RG_Tv">
                      <p class="RG_Al">{{ adv.title }}</p>
                      <p class="RG_BU">
                        {{ adv.intro }}
                      </p>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang='ts'>
import {
  defineComponent,
  ref,
  reactive,
  toRefs,
  nextTick,
  onErrorCaptured,
  onMounted,
  onBeforeMount,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  getCurrentInstance,
  computed,
  provide,
  watchEffect,
  onUnmounted,
} from "vue";
import { useStore } from "vuex";
import { onBeforeRouteUpdate, onBeforeRouteLeave } from "vue-router";
import { advages, subAdvages } from "./FC_Data";
const WINDDOW_HEIGHT =
  window.innerHeight || document.documentElement.clientHeight;
/** 數字的加總計時器 */
const TIMERS: number[] = [];

/** 優勢 */
export default defineComponent({
  name: "Advantage",
  setup() {
    const store = useStore();
    const screenWidth = computed(() => store.state.device.screenWidth);
    const isMobile = computed(() => store.state.device.isMobile);
    const clock = ref(null);

    /** 設置時鐘的動畫 */
    const setClockAnimate = () => {
      const target: any = clock;
      const OBJ = target.value;
      const CLIENT = OBJ.getBoundingClientRect();
      /** 物件的位置 */
      const OFFSET_Y = CLIENT.top;

      /** 看到物件之後，補上動畫css，否則移除 */
      if (OFFSET_Y <= WINDDOW_HEIGHT) {
        addClockAn();
        if (TIMERS.length === 0) setCountAn();
      } else {
        removeClockAn();
        TIMERS.forEach((ID) => {
          window.clearInterval(ID);
        });
        TIMERS.length = 0;
      }

      function addClockAn() {
        const clocks = OBJ.querySelectorAll(".RG_Ta");
        clocks.forEach((element: HTMLElement) => {
          const EXISTS = element.classList.contains("RG_Ef");
          if (!EXISTS) element.classList.add("RG_Ef");
        });
      }
      function removeClockAn() {
        const clocks = OBJ.querySelectorAll(".RG_Ta");
        clocks.forEach((element: HTMLElement) => {
          element.classList.remove("RG_Ef");
        });
      }
      function setCountAn() {
        const N = OBJ.querySelectorAll(".RG_TU > span");
        N.forEach((element: HTMLElement) => {
          element.innerText = "0";
          const TOTAL = Number(element.dataset.count);

          const ADD = () => {
            const CUR = Number(element.dataset.cur);
            if (element.innerText === "0") element.dataset.cur = "0";
            if (CUR < TOTAL) {
              const STEP = TOTAL / 40;
              element.dataset.cur = (CUR + STEP).toString();
              element.innerText = Math.ceil(CUR + STEP).toString();
            }
          };
          const ID = window.setInterval(ADD, 40);
          TIMERS.push(ID);
        });
      }
    };

    onMounted(() => {
      window.removeEventListener("scroll", setClockAnimate);
      window.addEventListener("scroll", setClockAnimate);
    });

    onUnmounted(() => {
      window.removeEventListener("scroll", setClockAnimate);
    });

    return {
      screenWidth,
      isMobile,
      clock,
      advages,
      subAdvages,
    };
  },
});
</script>